import { Component, OnInit } from '@angular/core';
import {Product} from "./model/product.model";
import {AdProductRepository} from "./service/ad-product-respository.service";

@Component({
  selector: 'sw-ad-bootstrap',
  templateUrl: './ad-bootstrap.component.html',
  styleUrls: ['./ad-bootstrap.component.css']
})
export class AdBootstrapComponent implements OnInit {
  products: Product[];

  constructor(private productRepository: AdProductRepository) {

  }

  ngOnInit() {
    this.productRepository.getProducts()
      .subscribe((data: Product[]) => {
        this.products = data;
        console.log(this.products);
      });
  }

}



