import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {AdProductRepository} from "../service/ad-product-respository.service";
import {Product} from "../model/product.model";

@Component({
  selector: 'app-ad-home',
  templateUrl: './ad-home.component.html',
  styleUrls: ['./ad-home.component.css']
})
export class AdHomeComponent implements OnInit {
  onLoad: boolean = false;
  products: Product[];

  constructor(private router: Router,
              private productRepository: AdProductRepository) {

  }

  ngOnInit() {
    this.productRepository.getProducts()
      .subscribe(_products => this.products = _products);
  }

  onClickGo() {
    this.router.navigate(['/angular-developer', 'product'])
  }

}



