import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {HttpModule} from "@angular/http";
import {SharedModule} from "./shared/shared.module";
import {RoutingModule} from "./routing.module";
import {AngularDeveloperModule} from "./shared/angular-developer/angular-developer.module";
import {HashLocationStrategy, LocationStrategy} from "@angular/common";
import {BACKEND_URL_TOKEN, DEV_SERVER_URL, HEROKU_SERVER_URL} from "./app.tokens";


@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    AngularDeveloperModule,
    BrowserModule,
    BrowserAnimationsModule,
    SharedModule,
    RoutingModule,
    /*testing*/
    HttpModule,
  ],
  providers: [
    {provide: LocationStrategy, useClass: HashLocationStrategy},
    {provide: BACKEND_URL_TOKEN, useValue: HEROKU_SERVER_URL},
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



